import React, { useState, useEffect } from 'react';
import { PasswordValidation } from './PasswordValidation';
import { TITLE_PREFIX } from '../../helpers/constants';
import { Token } from '../../helpers/token';
import { handleResponse } from '../../helpers/handleResponseErrors';
import { RouteComponentProps } from 'react-router';
import { cold } from 'react-hot-loader'

export const PasswordReset: React.FC<RouteComponentProps<{}>> = (props: RouteComponentProps<{}>) => {
    const [currentPassword, setCurrentPassword] = useState("");
    const [isCurrentPassword, setIsCurrentPassword] = useState(false);
    const [password, setPassword] = useState("");
    const [passwordCheck, setPasswordCheck] = useState("");
    const [isPasswordValid, setIsPasswordValid] = useState(false);
    const onChange_handleCurrentPassword = (event: React.ChangeEvent<HTMLInputElement>) => setCurrentPassword(event.target.value);
    const onChange_handlePassword = (event: React.ChangeEvent<HTMLInputElement>) => setPassword(event.target.value);
    const onChange_handlePasswordCheck = (event: React.ChangeEvent<HTMLInputElement>) => setPasswordCheck(event.target.value);
    const handlePasswordValidity = (isValid: boolean) => setIsPasswordValid(isValid);
    const checkCurrentPassword = () => {
        const userId = Token.getUserId();

        fetch(`api/User/CheckPassword/${userId}`, {
            method: 'POST',
            headers: Token.getAuthorizationHeaders(),
            body: JSON.stringify(currentPassword)
        })
            .then(response => handleResponse(props.history, response))
            .then(response => response.json() as boolean)
            .then(isCurrentPassword => setIsCurrentPassword(isCurrentPassword))
            .catch(error => console.log(error));
    }

    useEffect(() => { document.title = TITLE_PREFIX + "Password Reset"; }, []);

    let passwordFields;
    if (isCurrentPassword)
        passwordFields = (
            <>
                <label>Current Password</label>
                <input type="password" name="currentPassword" className="form-control input-md" placeholder="Current Password" value={ currentPassword } onChange={ e => onChange_handleCurrentPassword(e) } />
            </>
        );
    else
        passwordFields = (
            <PasswordValidation
                password={ password }
                passwordCheck={ passwordCheck }
                handlePasswordChange={ onChange_handlePassword }
                handlePasswordCheckChange={ onChange_handlePasswordCheck }
                handlePasswordValidity={ handlePasswordValidity }
            />    
        );
            
    return (
        <>
            { passwordFields }
        </>
    );
}


